:root {
  --main: #943ec5;
  --text: rgb(0, 0, 0, 0.8);
  --border: #ddd;
  --grey: #c4c4c4;
  --lightGrey: #f8f8f8;
  --issueBackground: #f8d7da;
  --issueText: #721c24;
  --fill: #9fa6b2;
  --link: #943ec5;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 24px;
  font-size: 14px;
}

h1 {
  margin: 0 0 10px 0;
  font-weight: 600;
  font-size: 16px;
  word-break: break-all;
}

p {
  margin: 10px 0 0 0;
}

b {
  font-weight: 600;
}

.tableWrap {
  border: 1px solid var(--border);
  border-radius: 4px;
}

table {
  width: 100%;
  table-layout: fixed;
  border-collapse: collapse;
  font-size: inherit;
  color: inherit;
  line-height: inherit;
}

tr:not(:last-child) {
  border-bottom: 1px solid var(--border);
}

td {
  padding: 10px;
  word-break: break-all;
}

td:first-child {
  width: 105px;
  font-weight: 600;
  word-break: normal;
}

td:nth-child(3) {
  width: 40px;
  font-weight: 600;
  word-break: normal;
}

ul {
  padding-left: 20px;
  margin: 0px;
}


@media screen and (max-width: 575px) {
  body {
    font-size: 13px;
  }

  h1 {
    font-size: 15px;
  }

  td:first-child {
    width: 70px;
  }
}
